import vue from 'vue'
import Router from 'vue-router'
import index from '../components/content/index-content-all' //
import order from '../components/content/order-content-all' //
import orders from '../components/content/order-page'       // > orders

export default new Router({
    
    mode:'history', // '#'
    routes:[
        {
            path:'*',
            redirect:'/'        //
        },
        {
            path:'/',component:index   //
        },
        {
            path:'/order',component:order,  //
            children:[
                {
                    path:'ORDES',      //
                    component:orders    //
                } 
            ]
        },
    ]
})